import React, { useState, useEffect } from "react"
import { Section, ButtonLink, Empty } from "components"
import { Form, Button, Row, Col, message, Popconfirm } from "antd"
import * as yup from "yup"
import uuid from "uuid/v4"
import styled from "styled-components"
import DataList from "./DataList"
import moment from "moment"
import { useMutation, useQuery } from "@apollo/react-hooks"
import { mutateUpdateExperiences, mutateDeleteExperiences } from "queries/profile"
import { newClient, mobile, client, renderError } from "helpers"
import { queryFetchSubjects, queryFetchLevels } from "queries/data"
import SelectInput from "components/forms/SelectInput"
import { Formik } from "formik"
import TextInput from "components/forms/TextInput"
import { DatePicker, SubmitButton } from "formik-antd"
import CheckboxInput from "components/forms/CheckboxInput"
import { Trans, t } from "@lingui/macro"
import { withI18n } from "@lingui/react"

const FieldRow = styled.div`
	position: relative;
	margin-bottom: 1.5em;
	padding-bottom: 2em;
	border-bottom: 1px solid #ddd;
	&:hover {
		.delete {
			display: flex;
			justify-content: center;
			align-items: center;
			visibility: visible;
		}
	}
	.delete {
		display: none;
		cursor: pointer;
		visibility: hidden;
		position: absolute;
		right: 0;
		border-radius: 50%;
		width: 25px;
		color: #ff9d00;
		height: 25px;
		z-index: 99;
		&:hover {
			background: #eee;
		}
	}
`

function Experiences({ onEditMode, onExperiences, unauthorizedUser, i18n, userData, ...props }) {
	const { fetchUser, loading } = props
	const { editMode, setEditMode } = onEditMode
	const { experiences } = onExperiences
	const [userExperiences, setUserExperiences] = useState([])
	const [isAdding, setIsAdding] = useState(false)

	const { data: dataCurriculums = {} } = useQuery(queryFetchLevels, {
		client: client.programmeClient,
		variables: { country: userData.country }
	})

	const { data: dataSubjects = {} } = useQuery(queryFetchSubjects, {
		client: client.programmeClient,
		variables: { country: userData.country }
	})

	const currOptions = (dataCurriculums.getProgrammeLevelList || []).map((item) => ({
		value: item.name,
		label: item.name
	}))

	const subjectOptions = (dataSubjects.getProgrammeSubjectList || []).map((item) => ({
		value: item.name,
		label: item.name
	}))

	const [updateExperiences] = useMutation(mutateUpdateExperiences, {
		client: newClient(),
		onCompleted: () => {
			fetchUser()
			setIsAdding(false)
			setEditMode("")
		},
		onError: (err) => renderError(err)
	})

	const [deleteExperiences] = useMutation(mutateDeleteExperiences, {
		client: newClient(),
		onCompleted: () => {
			fetchUser()
			setEditMode("")
			setIsAdding(false)
		},
		onError: (err) => renderError(err)
	})

	const details = (item) => {
		const { fromDate = "", toDate = "", subject = {}, isPrivateTutor } = item
		const monthFrom = fromDate.split("-")[1]
		const yearFrom = fromDate.split("-")[0]
		const monthTo = toDate.split("-")[1]
		const yearTo = toDate.split("-")[0]
		const totalExpMonths = moment(`${yearTo} ${monthTo}`).diff(moment(`${yearFrom} ${monthFrom}`), "months")
		const totalExp = Number((totalExpMonths / 12).toFixed(1))

		return (
			<section>
				{subject.level.map((l, idx) => (
					<p key={idx}>
						{l.curriculum}{" "}
						<span style={{ color: "#999" }}>{isPrivateTutor ? <Trans>Private tutor</Trans> : l.institution}</span>
					</p>
				))}
				<p style={{ marginBottom: 0 }}>
					{moment(monthFrom).format("MMM")} {yearFrom} - {moment(monthTo).format("MMM")} {yearTo} &middot;{" "}
					<span style={{ color: "#999" }}>
						<Trans>{totalExp}y experience</Trans>
					</span>
				</p>
			</section>
		)
	}

	const handleAddNewField = () => {
		setIsAdding(true)
		setUserExperiences((exps) => [
			...exps,
			{
				id: uuid(),
				isPrivateTutor: false,
				isPresent: false,
				institution: "",
				level: <Trans>Select your level</Trans>,
				subject: <Trans>Select your subject</Trans>,
				fromDate: "2019-01",
				toDate: "2019-01"
			}
		])
	}

	const handleSave = (values, { setSubmitting }) => {
		const hasPvid = values.hasOwnProperty("pvid")
		values = {
			...values,
			level: "0",
			institution: values.isPrivateTutor ? "-" : values.institution || "-",
			fromDate: moment(values.fromDate).format("YYYY-MM-DD"),
			toDate: values.isPresent ? moment().format("YYYY-MM-DD") : moment(values.toDate).format("YYYY-MM-DD")
		}
		if (hasPvid) {
			const { source, ...rest } = values
			values = rest
		}
		updateExperiences({ variables: { list: values } }).then(() => {
			message.success(i18n._(t`The experience item has been ${hasPvid ? i18n._(t`updated`) : i18n._(t`added`)}`), 2)
		})
		setSubmitting(false)
	}

	const handleCancel = () => {
		setUserExperiences(experiences)
		setEditMode("")
		setIsAdding(false)
	}

	const handleDelete = (pvid) => {
		const filtered = userExperiences.find((item) => item.pvid === pvid) || {}
		deleteExperiences({ variables: { pvidList: [filtered.pvid] } }).then(() => {
			message.success(i18n._(t`The experience item has been deleted`), 2)
		})
	}

	useEffect(() => {
		setUserExperiences(experiences)
	}, [experiences])

	// if (loading) return <Loading />

	return (
		<Section paddingHorizontal={0} noPadding={mobile}>
			{editMode === "experiences" ? (
				isAdding ? (
					<Formik
						onSubmit={handleSave}
						validationSchema={experiencesSchema(i18n)}
						initialValues={{ isPresent: false, isPrivateTutor: false }}
					>
						{({ handleSubmit, values }) => (
							<Form layout="vertical" onSubmit={handleSubmit}>
								<Row gutter={16} type="flex">
									<Col lg={8} xs={24}>
										<TextInput
											name="institution"
											placeholder={i18n._(t`Tuition's name`)}
											label={<Trans>Tuition's name</Trans>}
											disabled={values.isPrivateTutor}
										/>
									</Col>
									<Col lg={8} xs={12}>
										<SelectInput
											name="curriculum"
											label={<Trans>Curriculum</Trans>}
											placeholder={i18n._(t`Curriculum`)}
											options={currOptions}
										/>
									</Col>
									<Col lg={8} xs={12}>
										<SelectInput
											name="subject"
											label={<Trans>Subject</Trans>}
											placeholder={i18n._(t`Subject`)}
											options={subjectOptions}
										/>
									</Col>
								</Row>
								<Row gutter={16} type="flex" align="middle" style={{ marginBottom: "2em" }}>
									<Col lg={6} xs={12} className="mb2em-mobile">
										<DatePicker.MonthPicker
											name="fromDate"
											label={<Trans>Date joined</Trans>}
											placeholder={i18n._(t`Date joined`)}
											style={{ width: "100%" }}
											disabledDate={(current) => current && current > moment()}
										/>
									</Col>
									<Col lg={6} xs={12} className="mb2em-mobile">
										<DatePicker.MonthPicker
											name="toDate"
											label={<Trans>Date resigned</Trans>}
											placeholder={i18n._(t`Date resigned`)}
											style={{ width: "100%" }}
											disabledDate={(current) => current && current > moment()}
											disabled={values.isPresent}
										/>
									</Col>
									<Col lg={6} xs={24}>
										<CheckboxInput name="isPrivateTutor" className="mb0">
											<Trans>I was a private tutor</Trans>
										</CheckboxInput>
									</Col>
									<Col lg={6} xs={24}>
										<CheckboxInput name="isPresent" className="mb0">
											<Trans>I'm currently working here</Trans>
										</CheckboxInput>
									</Col>
								</Row>
								<SubmitButton block>
									<Trans>Save this experience</Trans>
								</SubmitButton>{" "}
								&nbsp;{" "}
								<ButtonLink block onClick={handleCancel}>
									<Trans>Cancel</Trans>
								</ButtonLink>
							</Form>
						)}
					</Formik>
				) : (
					<>
						{userExperiences.map((item, idx) => (
							<Formik key={idx} onSubmit={handleSave} initialValues={item}>
								{({ handleSubmit, values }) => (
									<Form layout="vertical" onSubmit={handleSubmit} style={{ marginBottom: "2em" }}>
										<FieldRow>
											<Row gutter={16}>
												<Col lg={8}>
													<TextInput
														name="institution"
														label={<Trans>Tuition's name</Trans>}
														placeholder={i18n._(t`Tuition's name`)}
														disabled={values.isPrivateTutor || item.isPrivateTutor}
													/>
												</Col>
												<Col lg={8}>
													<SelectInput
														name="curriculum"
														label={<Trans>Curriculum</Trans>}
														placeholder={i18n._(t`Curriculum`)}
														options={currOptions}
													/>
												</Col>
												<Col lg={8}>
													<SelectInput
														name="subject"
														label={<Trans>Subject</Trans>}
														placeholder={i18n._(t`Subject`)}
														options={subjectOptions}
													/>
												</Col>
											</Row>
											<Row gutter={16} type="flex" align="middle" style={{ marginBottom: "2em" }}>
												<Col lg={6} xs={12} className="mb2em-mobile">
													<DatePicker.MonthPicker
														name="fromDate"
														label={<Trans>Date joined</Trans>}
														placeholder={i18n._(t`Date joined`)}
														style={{ width: "100%" }}
													/>
												</Col>
												<Col lg={6} xs={12} className="mb2em-mobile">
													<DatePicker.MonthPicker
														name="toDate"
														label={<Trans>Date resigned</Trans>}
														placeholder={i18n._(t`Date resigned`)}
														disabled={values.isPresent || item.isPresent}
														style={{ width: "100%" }}
													/>
												</Col>
												<Col lg={6} xs={24}>
													<CheckboxInput name="isPrivateTutor" className="mb0">
														<Trans>I was a private tutor</Trans>
													</CheckboxInput>
												</Col>
												<Col lg={6} xs={24}>
													<CheckboxInput name="isPresent" className="mb0">
														<Trans>I'm currently working here</Trans>
													</CheckboxInput>
												</Col>
											</Row>
											<SubmitButton type="primary">
												<Trans>Update</Trans>
											</SubmitButton>{" "}
											&nbsp;{" "}
											<Popconfirm
												title={<Trans>Delete this experience?</Trans>}
												onConfirm={() => handleDelete(item.pvid)}
											>
												<ButtonLink icon="close">
													&nbsp; <Trans>Delete</Trans>
												</ButtonLink>
											</Popconfirm>
										</FieldRow>
									</Form>
								)}
							</Formik>
						))}
						<Button block type="dashed" size="medium" icon="plus" onClick={handleAddNewField} style={{ marginBottom: "2em" }}>
							&nbsp; <Trans>Add a new experience</Trans>
						</Button>
						<ButtonLink block icon="close" onClick={() => setEditMode(false)}>
							&nbsp; <Trans>Cancel</Trans>
						</ButtonLink>
					</>
				)
			) : (
				<>
					{userExperiences.length === 0 && !loading && (
						<Empty
							description={
								unauthorizedUser ? (
									<Trans>This tutor didn't set any tutoring experiences yet</Trans>
								) : (
									<Trans>You haven't set any tutoring experiences yet</Trans>
								)
							}
						/>
					)}

					{userExperiences.length > 0 &&
						userExperiences.map((item, idx) => {
							const { subject } = item

							return <DataList key={idx} loading={loading} title={`${subject.name}`} description={details(item)} />
						})}
				</>
			)}
		</Section>
	)
}

const experiencesSchema = (i18n) =>
	yup.object().shape({
		curriculum: yup.string().required(i18n._(t`You have to fill in your curriculum`)),
		subject: yup.string().required(i18n._(t`You have to fill in your subject`))
	})

export default withI18n()(Experiences)
