import React from "react"
import { Heading, ButtonLink } from "components"
import { Row, Col, Skeleton } from "antd"
import styled from "styled-components"
import { mobile, serverBaseUrl } from "helpers"
import { baseStyles } from "styles/base"
import { Trans } from "@lingui/macro"

const StyledHeading = styled(Heading)`
	&:not(:last-child) {
		border-bottom: 1px solid #eee;
		padding-bottom: 1em;
	}
`

const StyledRow = styled(Row)`
	&:not(:last-child) {
		border-bottom: 1px solid #eee;
		margin-bottom: 1em;
	}
`

const HeadingExtra = styled(Heading)`
	&& {
		h4 {
			font-size: 1em;
			color: ${baseStyles.greyColor};
		}
	}
`

const token = localStorage.getItem("token")

export default function DataList({ title = "", description = "", download = "", extra = "", loading }) {
	if (download) {
		return (
			<Skeleton loading={loading} active paragraph={{ rows: 1 }}>
				<StyledRow type="flex" justify="space-between">
					<Col xs={24} lg={12}>
						<Heading reverse content={title} subheader={description} />
					</Col>
					<Col xs={24} lg={12} style={{ textAlign: mobile ? "left" : "right" }}>
						{token && (
							<a href={serverBaseUrl + download} target="_blank" rel="noopener noreferrer">
								<ButtonLink icon="eye" style={{ marginRight: 0 }}>
									&nbsp; <Trans>View</Trans>
								</ButtonLink>
							</a>
						)}
					</Col>
				</StyledRow>
			</Skeleton>
		)
	}

	if (extra)
		return (
			<Skeleton loading={loading} active paragraph={{ rows: 1 }}>
				<StyledRow type="flex" justify="space-between" align="middle">
					<Col xs={24} lg={12}>
						<Heading reverse content={title} subheader={description} />
					</Col>
					<Col xs={24} lg={12} style={{ textAlign: mobile ? "left" : "right" }}>
						<HeadingExtra content={extra} level={4} />
					</Col>
				</StyledRow>
			</Skeleton>
		)

	return (
		<Skeleton loading={loading} active paragraph={{ rows: 1 }}>
			<StyledHeading reverse content={title} subheader={description} />
		</Skeleton>
	)
}
